import React from 'react';
import styled from 'styled-components';
import Link from 'next/link';
import { withRouter } from 'next/router';

const Header = styled.nav`
	ol {
		list-style: none;
		padding: 0;
		display: flex;
		justify-content: center;
		align-items: center;

		a {
			color: white;
			text-decoration: none;
		}

		> li:first-of-type {
			margin-right: auto;
			font-size: 1rem;
		}
	}
`;

const Title = styled.li``;

const ItemWrapper = styled.li`
	margin-right: 0.5rem;

	&:last-of-type {
		margin-right: 0;
	}

	a {
	}
`;

const Item = withRouter(({ children, router, href }) => {
	const handleClick = e => {
		e.preventDefault();
		router.push(href);
	};

	console.log(router);

	return (
		<ItemWrapper active={false}>
			<a href={href} onClick={handleClick}>
				{children}
			</a>
		</ItemWrapper>
	);
});

export default () => (
	<Header>
		<ol>
			<Title>
				<Link href="/">
					<a>explodingcamera</a>
				</Link>
			</Title>
			<Item>yay</Item>
			<Item>yay</Item>
			<Item>yay</Item>
		</ol>
	</Header>
);
